	module.exports = {"data":{"wordpressPost":{"title":"Paternal involvement in child-rearing, health anxiety, and the global epidemiology of alcohol use: Most read articles in November","content":"<h1>Paternal involvement in early child-rearing, health anxiety and risk of ischaemic heart disease, and the global epidemiology of alcohol use</h1>\n<p><img class=\"alignnone\" title=\"Image: Father and Child walking at sunset (311/335), by Negative Space\" src=\"https://www.goodfreephotos.com/albums/people/father-and-child-walking-at-sunset.jpg\" width=\"350\" height=\"233\" /></p>\n<p>The Top 10 Most Read list for November contains a number of new entries including papers on the reasons for retraction of articles, and the relationship between earlobe creases and coronary artery disease. At number one this month is a longitudinal study by Reece <em>et al</em>, which looks into the possibility that cannabis-exposed patients may be ageing more quickly. Entering the list at four and five respectively, are Farren <em>et al</em> and Berge <em>et al</em> with studies on the emotional distress in women after early pregnancy loss and the prospective association of health anxiety with ischaemic heart disease. A study on the involvement of fathers in early child-rearing on behaviour and development in their pre-adolescent children by Opondo <em>et al</em> reaches number six, finding that psychological and emotional aspects of paternal involvement in children&#8217;s early upbringing, particularly how new fathers see themselves as parents and adjust to the role, is associated with positive outcomes in children. It also received a <a href=\"http://blogs.bmj.com/bmjopen/2016/11/22/pre-teens-whose-dads-embrace-parenthood-may-be-less-prone-to-behavioural-issues/\">press release</a> and some attention on social media. Finally, returning at number eight this month, we have a popular article on birth cohort trends in the global epidemiology of alcohol use and alcohol-related harms in men and women by Slade <em>et al</em>, which concludes that the male-female gap in indicators of alcohol use and related harms is closing.</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Reece <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e011891.full\">Cannabis exposure as an interactive cardiovascular risk factor and accelerant of organismal ageing: a longitudinal study</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Ravnskov<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/6/e010401.full\">Lack of an association or an inverse association between low-density-lipoprotein cholesterol and mortality in the elderly: a systematic review</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Spoelman<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e013166.full\">Effect of an evidence-based website on healthcare usage: an interrupted time-series study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Farren <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e011864.full\">Post-traumatic stress, anxiety and depression following miscarriage or ectopic pregnancy: a prospective cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Berge <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e012914.full\">Health anxiety and risk of ischaemic heart disease: a prospective cohort study linking the Hordaland Health Study (HUSK) with the Cardiovascular Diseases in Norway (CVDNOR) project</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Opondo <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e012034.full\">Father involvement in early child-rearing and behavioural outcomes in their pre-adolescent children: evidence from the ALSPAC UK birth cohort</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Wang et<em> al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/2/e008558.full\">Relationship between diagonal earlobe creases and coronary artery disease as determined via angiography</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Slade<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/10/e011827.full\">Birth cohort trends in the global epidemiology of alcohol use and alcohol-related harms in men and women: systematic review and metaregression</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Hashem<em> et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e010874.full\">Cross-sectional survey of the amount of free sugars and calories in carbonated sugar-sweetened beverages on sale in the UK</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Moylan <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/6/11/e012047.full\">Why articles are retracted: a retrospective cross-sectional study of retraction notices at BioMed Central</a></td>\n</tr>\n</tbody>\n</table>\n<p><em>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.</em><!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"c39c7d74-501c-5349-a3ee-9b17d874c1d4"}}

